import React, {Component} from 'react';
import {connect} from 'react-redux';
import {CityInformation} from '../components/CityInformation';
import axios from 'axios';

class CityInformationComponent extends Component {
    constructor(props) {
        super(props);

        this.state = {
            expanded: null,
            description: null
        }
    }

    handleChange = (panel) => (event, expanded) => {
        this.setState({
            expanded: expanded ? panel : true
        })
    }

    getDescription = (city) => {
        console.log(city)
        axios.get('https://en.wikipedia.org/w/api.php?origin=*&action=query&list=search&srsearch=' + city +'&format=json')
    }

    render() {
        return (
            this.props.cities.map(element => {
                const description = this.getDescription(element)
                console.log(description)
                return <CityInformation 
                    city={element}
                    key={Date.now() + Math.random()}
                    handleChange={this.handleChange(element)}
                    expanded={this.state.expanded === element}
                    description={description}
                />
            })
        )
    }
}

const mapStateToProps = (state) => {
    return {
        cities: state.cities
    }
}

export default connect(mapStateToProps)(CityInformationComponent);